import {
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			houseType: ['整租', '合租'],
			currentHouseType: '整租',
			houseTypeindex: 0,
			pactImgFile: '',
			currentHouseTap: [],
			Price: 0,
			// houseNumShow: false,
			houseTap: [{
					value: '可以做饭',
					checked: false
				},
				{
					value: '提供热水',
					checked: false
				},
				{
					value: 'wifi上网',
					checked: false
				},
				{
					value: '独立卫生间',
					checked: false
				},
				{
					value: '近地铁',
					checked: false
				},
				{
					value: '周租月租',
					checked: false
				},
				{
					value: '自助入住',
					checked: false
				},
				{
					value: '空调',
					checked: false
				}
			],
			housePersonNum: ''
		}
	},
	onLoad() {

	},
	onShow() {

	},
	methods: {
		save() {
			var data = {}
			data.houseType = this.currentHouseType
			data.houseTap = this.currentHouseTap
			data.housePersonNum = this.housePersonNum
			data.Price = this.Price
			data.pactImgFile = this.pactImgFile
			console.log(data)
			this.saveHouseInfolist(data)
			uni.navigateBack()

		},
		upload() {
			var that = this
			uni.chooseImage({
				count: 1,
				sizeType: ['copressed'],
				success(res) {
					//因为有一张图片， 输出下标[0]， 直接输出地址
					that.pactImgFile = res.tempFilePaths[0]
				}
			})
		},
		checkboxChange: function(e) {
			var items = this.houseTap,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.includes(item.value)) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
				}
			}
			this.currentHouseTap = e.detail.value
		},
		ChangehouserType: function(e) {
			var that = this
			that.houseTypeindex = e.target.value
			that.currentHouseType = that.houseType[that.houseTypeindex]
		},
		inpPersonNum: function(e) {
			this.housePersonNum = e.detail.value
		},
		inpPrice: function(e) {
			this.Price = e.detail.value
		},
		...mapMutations(['saveHouseInfolist'])
	}
}
